import React, {useState} from 'react'
import {homeWorkReducer, sortAC, underageAC} from './bll/homeWorkReducer'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'

export type UserType = {
    _id: number
    name: string
    age: number
}

const initialPeople: Array<UserType> = [
    {_id: 0, name: '', age: 3},
    {_id: 1, name: '', age: 66},
    {_id: 2, name: '', age: 16},
    {_id: 3, name: '', age: 44},
    {_id: 4, name: '', age: 40},
    {_id: 5, name: '', age: 55},
]

function HW8() {
    const [people, setPeople] = useState<Array<UserType>>(initialPeople) // need to fix any

    // need to fix any
    const finalPeople = people.map((p: UserType) => (
        <div key={p._id}>
            <span>{p.name} </span>
            <span>{p.age}</span>
        </div>
    ))

    const sortUp = () => setPeople(homeWorkReducer(initialPeople, sortAC('up')))
    const sortDown = () => setPeople(homeWorkReducer(initialPeople, sortAC('down')))
    const UnderageCheck = () => setPeople(homeWorkReducer(initialPeople, underageAC(18)))

    return (
        <div>
            <hr/>
            homeworks 8

            {/*should work ( )*/}
            {finalPeople}

            <span><SuperButton onClick={sortUp}>sort up</SuperButton></span>
            <span><SuperButton onClick={sortDown}>sort down</SuperButton></span>
            <span><SuperButton onClick={UnderageCheck}>Underage check</SuperButton></span>

            <hr/>
            {/*  ,  */}
            {/*<AlternativePeople/>*/}
            <hr/>
        </div>
    )
}

export default HW8
